<template>
  <div class="barCharts-style">
    <div :id="id" class="charts-main"></div>
  </div>
</template>

<script>
/**
 * @3d
 * @authorbeixian.huang
 * @
 * chartsConfig{
 *     dimension:['xxxx','22222'],    //x
 *     quotaMap:{
 *      ''[11,1232,111],
 *      ''[11,1232,111],
 *     }   // key,
 *     colorMap{       // 
 *        '':['#31fff1','#27ccc1'], //
          '':['#0088d1','#025b95']
 *     }
 * }  
 * barTypestack/bar stackbar
 * barWidth
 * legendStatustrue/false, false
 * yAxiosDw : y%
 * changeAxiosx,y true/false, false
 * xVerticalx
 *showType3d(false)
 *isDiffColor:,,
 * percentRange :100%,,
 * isScroll:
 * */
import echarts from 'echarts';

export default {
  name: "bda-3d-barCharts",
  props: {
    chartsConfig: {
      type: Object,
      default: () => {
        return {}
      }
    },
    barType: {
      type: String,
      default: () => {
        return 'stack'
      }
    },
    barWidth: {
      type: Number,
      default: () => {
        return 3
      }
    },
    customizedStyle: {
      type: Number,
      default: () => {
        return 1
      }
    },
    legendStatus: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    yAxiosDw: {
      type: String,
      default: () => {
        return ''
      }
    },
    changeAxios: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    xVertical: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    showType: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isDiffColor: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    percentRange: {
      type: Array,
      default: () => {
        return []
      }
    },
    isScroll: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isSmallText: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    fontSizeShow: {
      type: Number,
      default: () => {
        return 0
      }
    }
  },
  data() {
    return {
      id: Math.random() + '_',
      legend: [],
      dataDetails: [],
      xData: [],
      series: [],
      drp: 3,
      fontSize: this.fontSizeShow !== 0 ? this.fontSizeShow : 15
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.xData = this.chartsConfig.dimension ? this.chartsConfig.dimension : [];
    let _winWidth = document.body.clientWidth;
    if (_winWidth > 1700) {
      if (this.xData.length <= 10) {
        this.drp = this.barWidth
      } else {
        this.drp = this.barWidth * 0.5
      }
    } else if (_winWidth <= 1700 && _winWidth >= 1400) {
      if (this.xData.length <= 10) {
        // this.fontSize = 14;
        this.fontSize = 14;
        this.drp = this.barWidth * 0.4;
      } else {
        this.fontSize = 14;
        this.drp = this.barWidth * 0.3;
      }
    } else {
      if (this.xData.length <= 10) {
        this.fontSize = 14;
        this.drp = this.barWidth * 0.3;
      } else {
        this.fontSize = 12;
        this.drp = this.barWidth * 0.2;
      }
    }
    if (this.isSmallText && this.xData.length > 10) this.fontSize = 10
    this.filterData(this.barType, this.chartsConfig.quotaMap, this.chartsConfig.colorMap);
    this.$nextTick(() => {
      this.init();
    })
  },
  methods: {
    filterData(type, data, colorMap) {
      let series = [];
      let count = -1;
      let dpr = this.drp;
      let that = this
      if (data) {
        window.console.log(data, 'chartsConfig')
        for (let key in data) {
          this.dataDetails.push(data[key]);
          this.legend.push(key);
          let colorGl = colorMap[key]
          count++;
          let _series = {
            type: 'bar',
            name: key,
            barWidth: 8 * dpr,
            itemStyle: {
              normal: {
                color:
                  new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      { offset: 0, color: colorGl[0], },
                      // { offset: 0.5, color: colorGl[1], },                    //
                      { offset: 1, color: colorGl[1], },                 //
                    ]
                  )
                ,
                barBorderRadius: [0, 0, 0, 0]
              }
            },
            data: this.dataDetails[count],
          };
          if (that.isDiffColor) {
            _series.itemStyle.normal.color =
              (params) => {
                if (that.isDiffColor) {
                  var colorList = colorGl;
                  var colorItem = colorList[params.dataIndex];
                  return new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    { offset: 0, color: colorItem[0], },
                    { offset: 0.5, color: colorItem[1], },                    //
                    { offset: 1, color: colorItem[1], },
                  ], false);

                }
              }
          }
          let _glSeries = {
            name: key,
            tooltip: {
              show: false
            },
            type: 'bar',
            barWidth: 2 * dpr,
            itemStyle: {
              normal: {
                color: colorGl[1],
                barBorderRadius: [0, 0, 0, 0]
              }
            },
            data: this.dataDetails[count],

            legendHoverLink: false
          };
          if (type === 'stack') {
            _series.stack = 'stack';
            if (this.showType) _glSeries.stack = 'stack2';
            if (this.showType) _glSeries.barGap = 0;
          }
          series.push(_series);
          if (this.showType) series.push(_glSeries)
        }
      }
      this.series = series;
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let xData = this.xData;
      let $this = this;
      let xAxios = {
        data: xData,
        axisLabel: {
          interval: 0,
          show: true,
          fontSize: this.fontSize + 2,
          fontFamily: 'Noto Sans SC',
          formatter: function (value) {

            if (xData.length > 10 || $this.xVertical) {

              return value.split("").join("\n");
            } else {
              if ($this.changeAxios) {
                if (value.length > 4) {
                  return value.substring(0, 4) + '...'
                } else {
                  return value
                }
              } else {
                var newParamsName = ""; // 
                var paramsNameNumber = value.length; // 
                var provideNumber = 2; // 
                if ($this.barWidth > 4) {
                  provideNumber = 4
                }
                var rowNumber = Math.ceil(paramsNameNumber / provideNumber); // 
                /**
                 *   
                 */
                // rowNumber>1
                if (paramsNameNumber > provideNumber) {
                  /** ,p */
                  for (var p = 0; p < rowNumber; p++) {
                    var tempStr = ""; // 
                    var start = p * provideNumber; // 
                    var end = start + provideNumber; // 
                    // 
                    if (p == rowNumber - 1) {
                      // 
                      tempStr = value.substring(start, paramsNameNumber);
                    } else {
                      // 
                      tempStr = value.substring(start, end) + "\n";
                    }
                    newParamsName += tempStr; // 
                  }

                } else {
                  // 
                  newParamsName = value;
                }
                //
                return newParamsName
              }
            }
          }
        },
        splitLine: {
          show: false,
          lineStyle: {
            color: 'rgba(255,255,255,.1)'
          }
        },
        axisLine: {
          lineStyle: {
            color: '#fff',
            fontSize: this.fontSize
          }
        }
      };
      let _width = document.body.clientWidth;
      if (_width <= 1700 && _width >= 1400) {
        if (xData.length > 8 && xData.length <= 10) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.9;
        } else if (xData.length > 10) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.8;
        }
        if (this.customizedStyle !== 1) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.74;
        }
      }
      let dw = this.yAxiosDw;
      let options = {
        tooltip: {
          formatter: function (a) {
            return a.name + '<br>' + a.seriesName + '' + a.value + dw
          }
        },
        grid: {
          top: this.legendStatus ? 30 : 10,
          left: 50,
          right: '5%',
          bottom: !this.isScroll ? '25%' : '30%',
        },
        legend: {
          show: this.legendStatus,
          data: this.legend,
          textStyle: {
            color: '#fff',
            fontFamily: 'Noto Sans SC',
            fontSize: this.fontSize,
          },
          right: 20,
          top: 0
        },
        dataZoom: this.isScroll ? [//
          {
            xAxisIndex: 0,//X0
            show: false,//
            type: 'slider', //  dataZoom  slider  dataZoom 
            startValue: 0, // 
            endValue: 9  // 6
          }
        ] : [],

        xAxis: xAxios,
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(255,255,255,.1)'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          axisLabel: {
            show: true,
            fontSize: this.fontSize,
            formatter: function (value) {
              return value + dw
              // if (value>=1000){
              //     if (value%1000===0){
              //         return (value/1000)+'K'+dw
              //     }else {
              //         return (value/1000).toFixed(2)+'K'+dw
              //     }
              // }else {
              //     return value+dw
              // }
            },
            interval: 0,
            rotate: 0
          }
        },

        series: this.series
      };
      if (this.percentRange) options.yAxis.max = this.percentRange[1]
      if (this.percentRange) options.yAxis.min = this.percentRange[0]
      if (this.changeAxios === true) {
        options.xAxis = options.yAxis;
        options.yAxis = xAxios;
        let xLen = 0;
        if (xData) {
          for (let v of xData) {
            if (v.length >= xLen) {
              xLen = v.length;
            }
          }
        }
        if (xLen >= 4) {
          options.grid.left = 80;
        } else {
          options.grid.left = 50;
        }
        if (this.legendStatus === false) {
          options.grid.top = 10;
        }
      }
      // charts.resize()
      charts.setOption(options)

      window.console.log(options, 'options222')
      if (this.isScroll) {
        let start = 0
        let end = 9
        setInterval(() => {
          if (end <= this.dataDetails[0].length) {
            charts.setOption({
              dataZoom: {
                startValue: start++,
                endValue: end++
              }
            })
          } else {
            start = 0
            end = 9
          }
        }, 2000)
      }
    }
  }
}
</script>

<style scoped lang="scss">
.barCharts-style {
  position: relative;
  width: 100%;
  height: 100%;

  .noDataStyle {
    line-height: 50px;
    font-size: 14px;
    text-align: center;
  }

  .charts-main {
    width: 100%;
    height: 100%;
  }

  .legend-style {
    position: absolute;
    right: 24px;
    font-size: 14px;
    top: 10px;
    z-index: 999;

    label {
      display: block;
      float: left;
      margin-right: 10px;
      cursor: pointer;

      .colorPick {
        width: 20px;
        height: 10px;
        display: block;
        float: left;
        border-radius: 3px;
        margin-right: 5px;
        margin-top: 5px;
        cursor: pointer;
      }

      .keyName {
        display: block;
        float: left;
        cursor: pointer;
      }
    }
  }
}
</style>